.fetching-spinner-wrapper{
    position: absolute;
    top: 115px;
    left: 50%;
    transform: translateX(-50%);
}
.fetching-spinner-wrapper > i {
    animation: mymove 2s infinite linear;
    color: #fff;
}

@keyframes mymove {
    100% {-webkit-transform: rotate(360deg); transform:rotate(360deg);}
}